body {
  background: #fdfdfd;
}

.form-container {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 460px;
}

.invalid-feedback {
  display: block;
  width: 100%;
  margin-top: 0.25rem;
  font-size: 80%;
  color: #dc3545;
}

.steps {
  /* height: 65px; */
  display: flex;
  justify-content: space-between;
  /* margin-bottom: 0; */
}

.step {
  display: flex;
  position: relative;
  flex-direction: column;
  flex-grow: 1;
  flex-basis: 0;
  align-items: center;
}

.step p {
  margin-bottom: 0;
}

.step.is-active .step__marker {
  background: #19ddfe;
  border: 4px solid #f26120;
}

.step.is-completed .step__marker {
  background: #f26120;
  border: 4px solid #f26120;
}

.step__marker {
  width: 35px;
  height: 35px;
  display: flex;
  align-items: center;
  border-radius: 50%;
  font-weight: 700;
  justify-content: center;
  background: #b5b5b5;
  color: #fff;
  border: 4px solid #fff;
  z-index: 1;
}

.step.is-active .step__marker {
  background: #19ddfe;
  border: 4px solid #f26120;
}

.step__title {
  font-weight: 700;
}

.step.is-completed:not(:first-child):before {
  background: #f26120;
}

.step.is-active:not(:first-child):before {
  background: #f26120;
}

.step:not(:first-child):before {
  height: 0.2em;
  width: 100%;
  bottom: 0;
  left: -50%;
  top: 1rem;
  content: " ";
  display: block;
  position: absolute;
  background: #b5b5b5;
}

.step4__img {
  height: 410px;
  width: 100%;
}

.step-visible {
  display: block;
}

.step-hidden {
  display: none;
}

.card-body {
  flex: 1 1 auto;
  padding: 1.25rem;
}

img {
  width: 100%;
}
